//libs
import React from "react";
import { CountryDropdown, RegionDropdown } from "react-country-region-selector";
//consts
import { Images } from "shared/images";
import {
  FILTERS_ENUM,
  FILTER_TYPES,
  LABEL,
  STRINGS,
  docStatusUpdate,
} from "./constants";
//styles
import "./style.scss";
import CustomButton from "../CustomButton";
import DateRangeSelector from "components/atom/dateRange";
import RangeSlider from "../RangeSlider";
import { VACANCY_TYPE } from "shared/constants";
import { statusUpdate } from "components/atom/searchBar/filterConsts";
import ReactSelect from "components/atom/ReactSelect";

function FiltersCustom({
  filterData = {},
  toggle = false,
  handleChangeFilterData = () => {},
  reset = () => {},
  handleFetchDataForFilter = () => {},
  type = VACANCY_TYPE.DASHBOARD,
}) {
  const filterButtonsDashboard = [
    {
      title: "Activation Status",
      type: FILTER_TYPES.DROPDOWN,
      data: statusUpdate,
      id: FILTERS_ENUM.blocked,
      value: filterData?.activation,
      handleChange: (value) => {
        handleChangeFilterData("activation", value);
      },
    },
    {
      title: "Document Status",
      type: FILTER_TYPES.DROPDOWN,
      data: docStatusUpdate,
      id: FILTERS_ENUM.blocked,
      value: filterData?.document,
      handleChange: (value) => {
        handleChangeFilterData("document", value);
      },
    },
  ];

  return (
    <>
      {toggle ? (
        <div className={`card filters_col mt-3 ${!toggle ? "d-none" : ""}`}>
          <div className="card-body bg-white">
            <div className="row">
              {type !== VACANCY_TYPE.BRANCHES ? (
                <>
                  <div className="col-md-4 mb-3 position-relative">
                    <label>{LABEL.COUNTRY}</label>
                    <div className="country_picker ">
                      <CountryDropdown
                        value={filterData["country"]}
                        onChange={(val) => {
                          handleChangeFilterData("country", val);
                        }}
                        defaultOptionLabel={LABEL.SELECT}
                      />
                    </div>
                  </div>
                  <div className="col-md-4 mb-3 position-relative">
                    <label>{LABEL.STATE}</label>
                    <div className="country_picker">
                      <RegionDropdown
                        country={filterData["country"]}
                        value={filterData["state"]}
                        onChange={(val) => {
                          handleChangeFilterData("state", val);
                        }}
                        defaultOptionLabel={LABEL.SELECT}
                        blankOptionLabel={LABEL.SELECT}
                      />
                    </div>
                  </div>
                </>
              ) : null}
              {type !== VACANCY_TYPE.USER ? (
                <>
                  <div className="col-md-4 mb-3">
                    <DateRangeSelector
                      dateRange={{
                        startDate: filterData["selectedDate"].from || null,
                        endDate: filterData["selectedDate"].to || null,
                        key: "selection",
                      }}
                      setDateRange={(val) =>
                        handleChangeFilterData("selectedDate", {
                          from: val.startDate,
                          to: val.endDate,
                        })
                      }
                      titleText={LABEL.START_DATE}
                      icon={Images.calendar}
                    />
                  </div>
                  <div className="col-md-4 mb-3">
                    <RangeSlider
                      state={filterData["rateRange"]}
                      changeHandler={(val) =>
                        handleChangeFilterData("rateRange", val)
                      }
                      titleText={STRINGS.RATE}
                    />
                  </div>
                </>
              ) : (
                <>
                  {filterButtonsDashboard?.map((i, idx) => {
                    return (
                      <div className="col-md-4 mb-3">
                        <label className="d-block text-capitalize">
                          {i.title}
                        </label>
                        <div className="size-md ">
                          <ReactSelect
                            options={i.data}
                            handleChange={i.handleChange}
                            selectedOption={i?.value}
                            title={"Select"}
                            classNamePrefix="react-prefix"
                            className="react-select"
                          />
                        </div>
                      </div>
                    );
                  })}
                </>
              )}
              <div className="col-2 mb-3">
                <label className="d-none d-md-block">&nbsp;</label>
                <div className="btn_grp">
                  <CustomButton
                    Icon={Images.reset}
                    className="btn btn-sm btn-primary ms-2"
                    onClick={reset}
                  />
                  <CustomButton
                    Icon={Images.whiteTick}
                    className="btn btn-sm btn-secondary ms-2"
                    onClick={handleFetchDataForFilter}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      ) : null}
    </>
  );
}

export default FiltersCustom;
